import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
    updateWebcam
} from '../../redux/system/actions';
import { withRouter } from "react-router";
import cn from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import { styles } from '../../theme/AppShell.Style';
import {
    Card,
    CardHeader,
} from '@material-ui/core/';
import { PiPlayer } from '../'

class Webcam extends Component {

    state = {
        timer: null,
    }

    componentDidMount() {
        this.startTimer();
    }

    componentWillUnmount() {
        this.stopTimer();
    }

    shouldComponentUpdate(e) {
        return true;
    }

    startTimer = () => {
        const {
            webcam
        } = this.props;
        const { timerDelay } = webcam;
        const {
            timer
        } = this.state;
        if (!timer) {
            this.setState({ timer: setInterval(this.updateTimer, timerDelay * 1000) });
        }
    }

    stopTimer = () => {
        console.log('stopTimer');
        const {
            timer
        } = this.state;
        if (timer) {
            clearInterval(timer);
            this.setState({ timer: null });
        }
    }

    updateTimer = () => {
        updateWebcam();
    }

    render() {
        const {
            classes,
            // mode,
            // history,
        } = this.props;
        // let screenMode = `full`;
        // if (mode) {
        //     screenMode = mode;
        // } else {
        //     alert('Webcam mode not specified');
        //     return null;
        // }
        return (
            <Card className={cn(classes.screenCard)}>
                <CardHeader
                    title={`Webcam`}
                />
                <PiPlayer />
            </Card>
        );
    }
}

const mapStateToProps = (store) => {
    return {
        store,
        webcam: store.system.webcam
    };
};

export default (connect(
    mapStateToProps,
    null
)(withStyles(styles, { withTheme: true })(withRouter(Webcam))));
